  app.lang.translatePhrase = function(phraseIdentifier, languageIdentifier) {
    var map;
    if (languageIdentifier in app.lang.phrases) {
      map = app.lang.phrases[languageIdentifier];
      if (phraseIdentifier in map) {
        return map[phraseIdentifier];
      }
    }
    return phraseIdentifier;
  };

  app.lang.translateNumeral = function(numeral, languageIdentifier) {
    var digit, i, len, map, newNumeral;
    if (languageIdentifier in app.lang.numerals) {
      map = app.lang.numerals[languageIdentifier];
      numeral = ('' + numeral).split('');
      newNumeral = [];
      for (i = 0, len = numeral.length; i < len; i++) {
        digit = numeral[i];
        if (!(digit in map)) {
          return 'TRANSLATION_FAILED FOR "' + digit + '"';
        }
        newNumeral.push(map[digit]);
      }
      return newNumeral.join('');
    }
    return '' + numeral;
  };

